.profile-area{
    padding: 70px 0;
    font-family: 'Poppins', sans-serif;
    background: #FDF5E6;
}

.card1{
    box-shadow: 0 0 30px rgba(0,0,0,0.5 );
    overflow: hidden;
    border-radius: 15px;
    height: 380px;
  
}
.img1{
    height: 250px;
    border-top-right-radius:15px ;
    border-top-left-radius: 15px;
    width: 100%;
}
.card-body{
    padding: 30px 0;
    text-align: center;
}
.card-title{
    text-transform: uppercase;
    font-weight: 900;
    font-size: 20px;
    margin: 0 0 10px;
    text-decoration: none;
    font-size: 16px;
    color: #121212;
}
.card-rating{
    font-size: 16px;
    padding-bottom: 0 35px;
}

.searchMargin{
    margin-top: 80px;
    margin-bottom: 39px;

}
.sbar{
    margin-left: auto;
    margin-right: auto;
}
.searchIcons{
    margin: 7px;
    height: 23px;

    
}

@media (max-width: 576px) {
    .card{
       margin-left: 33px;
       margin-right: 33px;
      
    }
    
}

@media (max-width: 400px) {
    .card{
       margin-left: 5px;
       margin-right: 5px;
      
    }
    
}





